import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { toast } from 'react-toastify';
import { 
  Box, 
  Typography, 
  Paper, 
  Grid, 
  TextField, 
  Button, 
  Stepper, 
  Step, 
  StepLabel,
  FormControlLabel,
  Checkbox,
  Radio,
  RadioGroup,
  FormControl,
  FormLabel,
  Divider,
  CircularProgress,
  Card,
  CardContent,
  Alert
} from '@mui/material';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { he } from 'date-fns/locale';
import { addDays, differenceInDays } from 'date-fns';

const steps = [' ', ' ', ' ', ''];

const BookingPage = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const [activeStep, setActiveStep] = useState(0);
  const [room, setRoom] = useState(null);
  const [loading, setLoading] = useState(true);
  const [checkingAvailability, setCheckingAvailability] = useState(false);
  const [error, setError] = useState(null);
  
  //  
  const [bookingData, setBookingData] = useState({
    roomId: location.state?.roomId || '',
    checkIn: null,
    checkOut: null,
    guest: {
      name: '',
      phone: '',
      email: ''
    },
    isTourist: false,
    paymentMethod: 'credit',
    creditCardDetails: {
      cardNumber: '',
      expiryDate: '',
      cvv: ''
    },
    notes: ''
  });
  
  // 
  const [calculations, setCalculations] = useState({
    nights: 0,
    basePrice: 0,
    vatAmount: 0,
    totalPrice: 0
  });

  //   
  useEffect(() => {
    const fetchRoom = async () => {
      if (!bookingData.roomId) {
        //    ,     ( 6)
        try {
          const response = await axios.get(`${process.env.REACT_APP_API_URL}/rooms`);
          const rooms = response.data.data;
          const standardRoom = rooms.find(room => room.roomNumber === 6);
          
          if (standardRoom) {
            setRoom(standardRoom);
            setBookingData(prev => ({ ...prev, roomId: standardRoom._id }));
          } else {
            setError('   .     .');
          }
        } catch (error) {
          console.error('  :', error);
          setError('     .     .');
        }
      } else {
        //    ,     
        try {
          const response = await axios.get(`${process.env.REACT_APP_API_URL}/rooms/${bookingData.roomId}`);
          setRoom(response.data.data);
        } catch (error) {
          console.error('  :', error);
          setError('     .     .');
        }
      }
      setLoading(false);
    };

    fetchRoom();
  }, [bookingData.roomId]);

  //        
  useEffect(() => {
    if (room && bookingData.checkIn && bookingData.checkOut) {
      const nights = differenceInDays(bookingData.checkOut, bookingData.checkIn);
      const basePrice = room.basePrice * nights;
      const vatAmount = bookingData.isTourist ? 0 : basePrice * 0.17;
      const totalPrice = basePrice + vatAmount;
      
      setCalculations({
        nights,
        basePrice,
        vatAmount,
        totalPrice
      });
    }
  }, [room, bookingData.checkIn, bookingData.checkOut, bookingData.isTourist]);

  //   
  const checkAvailability = async () => {
    if (!bookingData.checkIn || !bookingData.checkOut || !bookingData.roomId) {
      return false;
    }
    
    setCheckingAvailability(true);
    
    try {
      const response = await axios.post(`${process.env.REACT_APP_API_URL}/rooms/check-availability`, {
        roomId: bookingData.roomId,
        checkIn: bookingData.checkIn,
        checkOut: bookingData.checkOut
      });
      
      setCheckingAvailability(false);
      return response.data.isAvailable;
    } catch (error) {
      console.error('  :', error);
      setCheckingAvailability(false);
      return false;
    }
  };

  //   
  const handleChange = (e) => {
    const { name, value, checked, type } = e.target;
    
    if (name.includes('.')) {
      const [parent, child] = name.split('.');
      setBookingData(prev => ({
        ...prev,
        [parent]: {
          ...prev[parent],
          [child]: type === 'checkbox' ? checked : value
        }
      }));
    } else {
      setBookingData(prev => ({
        ...prev,
        [name]: type === 'checkbox' ? checked : value
      }));
    }
  };

  //   
  const handleDateChange = (name, date) => {
    setBookingData(prev => ({
      ...prev,
      [name]: date
    }));
  };

  //   
  const handleNext = async () => {
    //     
    if (activeStep === 0) {
      //  
      if (!bookingData.checkIn || !bookingData.checkOut) {
        toast.error('   \'- \'-');
        return;
      }
      
      if (differenceInDays(bookingData.checkOut, bookingData.checkIn) <= 0) {
        toast.error(' \'-      \'-');
        return;
      }
      
      //  
      const isAvailable = await checkAvailability();
      if (!isAvailable) {
        toast.error('    .    .');
        return;
      }
    } else if (activeStep === 1) {
      //   
      const { name, phone, email } = bookingData.guest;
      if (!name || !phone || !email) {
        toast.error('     ');
        return;
      }
      
      //    
      if (!email.includes('@') || !email.includes('.')) {
        toast.error('    ');
        return;
      }
    } else if (activeStep === 2) {
      //   
      if (bookingData.paymentMethod === 'credit') {
        const { cardNumber, expiryDate, cvv } = bookingData.creditCardDetails;
        if (!cardNumber || !expiryDate || !cvv) {
          toast.error('      ');
          return;
        }
        
        //   
        if (cardNumber.replace(/\s/g, '').length < 14) {
          toast.error('    ');
          return;
        }
        
        if (cvv.length < 3) {
          toast.error('  (CVV)  ');
          return;
        }
      }
    }
    
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  //   
  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  //  
  const handleSubmit = async () => {
    try {
      setLoading(true);
      
      const response = await axios.post(`${process.env.REACT_APP_API_URL}/bookings`, {
        roomId: bookingData.roomId,
        guest: bookingData.guest,
        checkIn: bookingData.checkIn,
        checkOut: bookingData.checkOut,
        isTourist: bookingData.isTourist,
        paymentMethod: bookingData.paymentMethod,
        creditCardDetails: bookingData.paymentMethod === 'credit' ? bookingData.creditCardDetails : undefined,
        notes: bookingData.notes
      });
      
      toast.success('  !');
      setActiveStep(steps.length);
    } catch (error) {
      console.error('  :', error);
      toast.error(error.response?.data?.message || '  .     .');
    } finally {
      setLoading(false);
    }
  };

  //   
  const handleReset = () => {
    navigate('/');
  };

  //   1 -  
  const renderDateSelection = () => (
    <Box>
      <Typography variant="h6" gutterBottom>
          
      </Typography>
      
      <Grid container spacing={3}>
        <Grid item xs={12} sm={6}>
          <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={he}>
            <DatePicker
              label=" '-"
              value={bookingData.checkIn}
              onChange={(date) => handleDateChange('checkIn', date)}
              disablePast
              renderInput={(params) => <TextField {...params} fullWidth required />}
            />
          </LocalizationProvider>
        </Grid>
        <Grid item xs={12} sm={6}>
          <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={he}>
            <DatePicker
              label=" '-"
              value={bookingData.checkOut}
              onChange={(date) => handleDateChange('checkOut', date)}
              minDate={bookingData.checkIn ? addDays(bookingData.checkIn, 1) : addDays(new Date(), 1)}
              renderInput={(params) => <TextField {...params} fullWidth required />}
            />
          </LocalizationProvider>
        </Grid>
      </Grid>
      
      {bookingData.checkIn && bookingData.checkOut && (
        <Box sx={{ mt: 3 }}>
          <Typography variant="subtitle1">
            :
          </Typography>
          <Typography>
             : {calculations.nights}
          </Typography>
          <Typography>
             : {calculations.basePrice} 
          </Typography>
        </Box>
      )}
    </Box>
  );

  //   2 -  
  const renderGuestDetails = () => (
    <Box>
      <Typography variant="h6" gutterBottom>
         
      </Typography>
      
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <TextField
            required
            fullWidth
            label=" "
            name="guest.name"
            value={bookingData.guest.name}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required
            fullWidth
            label=""
            name="guest.phone"
            value={bookingData.guest.phone}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required
            fullWidth
            label=""
            name="guest.email"
            type="email"
            value={bookingData.guest.email}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12}>
          <FormControlLabel
            control={
              <Checkbox
                name="isTourist"
                checked={bookingData.isTourist}
                onChange={handleChange}
                color="primary"
              />
            }
            label="  ( )"
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            fullWidth
            label=" "
            name="notes"
            multiline
            rows={3}
            value={bookingData.notes}
            onChange={handleChange}
          />
        </Grid>
      </Grid>
    </Box>
  );

  //   3 -  
  const renderPaymentDetails = () => (
    <Box>
      <Typography variant="h6" gutterBottom>
         
      </Typography>
      
      <FormControl component="fieldset" sx={{ mb: 3 }}>
        <FormLabel component="legend"> </FormLabel>
        <RadioGroup
          name="paymentMethod"
          value={bookingData.paymentMethod}
          onChange={handleChange}
        >
          <FormControlLabel value="credit" control={<Radio />} label=" " />
          <FormControlLabel value="cash" control={<Radio />} label=" ()" />
          <FormControlLabel value="bank_transfer" control={<Radio />} label=" " />
        </RadioGroup>
      </FormControl>
      
      {bookingData.paymentMethod === 'credit' && (
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <TextField
              required
              fullWidth
              label="  "
              name="creditCardDetails.cardNumber"
              value={bookingData.creditCardDetails.cardNumber}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              required
              fullWidth
              label=" (MM/YY)"
              name="creditCardDetails.expiryDate"
              value={bookingData.creditCardDetails.expiryDate}
              onChange={handleChange}
              placeholder="MM/YY"
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              required
              fullWidth
              label="  (CVV)"
              name="creditCardDetails.cvv"
              value={bookingData.creditCardDetails.cvv}
              onChange={handleChange}
              type="password"
            />
          </Grid>
        </Grid>
      )}
      
      {bookingData.paymentMethod === 'bank_transfer' && (
        <Alert severity="info" sx={{ mt: 2 }}>
                   .
        </Alert>
      )}
      
      {bookingData.paymentMethod === 'cash' && (
        <Alert severity="info" sx={{ mt: 2 }}>
               .
        </Alert>
      )}
    </Box>
  );

  //   4 - 
  const renderSummary = () => (
    <Box>
      <Typography variant="h6" gutterBottom>
         
      </Typography>
      
      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Typography variant="subtitle1" gutterBottom>
             :
          </Typography>
          <Typography>
             {room.roomNumber} - {room.type === 'standard' ? '' : room.type}
          </Typography>
          <Typography>
             '-: {bookingData.checkIn.toLocaleDateString('he-IL')}
          </Typography>
          <Typography>
             '-: {bookingData.checkOut.toLocaleDateString('he-IL')}
          </Typography>
          <Typography>
             : {calculations.nights}
          </Typography>
          
          <Divider sx={{ my: 2 }} />
          
          <Typography variant="subtitle1" gutterBottom>
             :
          </Typography>
          <Typography>
            : {bookingData.guest.name}
          </Typography>
          <Typography>
            : {bookingData.guest.phone}
          </Typography>
          <Typography>
            : {bookingData.guest.email}
          </Typography>
          
          <Divider sx={{ my: 2 }} />
          
          <Typography variant="subtitle1" gutterBottom>
             :
          </Typography>
          <Typography>
             : {
              bookingData.paymentMethod === 'credit' ? ' ' :
              bookingData.paymentMethod === 'cash' ? '' : ' '
            }
          </Typography>
          <Typography>
             : {calculations.basePrice.toFixed(2)} 
          </Typography>
          <Typography>
            " ({bookingData.isTourist ? '' : '17%'}): {calculations.vatAmount.toFixed(2)} 
          </Typography>
          <Typography variant="h6" sx={{ mt: 1 }}>
            " : {calculations.totalPrice.toFixed(2)} 
          </Typography>
        </CardContent>
      </Card>
      
      <Alert severity="info">
          ,       .
      </Alert>
    </Box>
  );

  //   
  const renderComplete = () => (
    <Box sx={{ textAlign: 'center', py: 3 }}>
      <Typography variant="h5" gutterBottom>
          !
      </Typography>
      <Typography variant="subtitle1" paragraph>
           .      .
      </Typography>
      <Typography paragraph>
         : {/*       */}
      </Typography>
      <Button
        variant="contained"
        onClick={handleReset}
        sx={{ mt: 3 }}
      >
          
      </Button>
    </Box>
  );

  //    
  const getStepContent = (step) => {
    switch (step) {
      case 0:
        return renderDateSelection();
      case 1:
        return renderGuestDetails();
      case 2:
        return renderPaymentDetails();
      case 3:
        return renderSummary();
      default:
        return '  ';
    }
  };

  if (loading && !room) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
        <CircularProgress />
      </Box>
    );
  }

  if (error) {
    return (
      <Box sx={{ textAlign: 'center', mt: 4 }}>
        <Typography variant="h5" color="error" gutterBottom>
          {error}
        </Typography>
        <Button 
          variant="contained" 
          onClick={() => navigate('/')}
          sx={{ mt: 2 }}
        >
            
        </Button>
      </Box>
    );
  }

  return (
    <Box>
      <Typography variant="h4" component="h1" gutterBottom align="center">
         
      </Typography>
      
      <Paper sx={{ p: 3, mb: 4 }}>
        {activeStep === steps.length ? (
          renderComplete()
        ) : (
          <>
            <Stepper activeStep={activeStep} sx={{ mb: 4 }}>
              {steps.map((label) => (
                <Step key={label}>
                  <StepLabel>{label}</StepLabel>
                </Step>
              ))}
            </Stepper>
            
            {getStepContent(activeStep)}
            
            <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 3 }}>
              <Button
                disabled={activeStep === 0 || loading || checkingAvailability}
                onClick={handleBack}
              >
                
              </Button>
              
              {activeStep === steps.length - 1 ? (
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleSubmit}
                  disabled={loading || checkingAvailability}
                >
                  {loading ? <CircularProgress size={24} /> : ' '}
                </Button>
              ) : (
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleNext}
                  disabled={loading || checkingAvailability}
                >
                  {checkingAvailability ? <CircularProgress size={24} /> : ''}
                </Button>
              )}
            </Box>
          </>
        )}
      </Paper>
    </Box>
  );
};

export default BookingPage; 