import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { 
  Typography, 
  Box, 
  Button, 
  Card, 
  CardMedia, 
  CardContent, 
  CardActions,
  Grid,
  Paper,
  Container,
  Divider
} from '@mui/material';
import { 
  Hotel as HotelIcon,
  LocationOn as LocationIcon,
  Phone as PhoneIcon
} from '@mui/icons-material';

const HomePage = () => {
  const [room, setRoom] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchRoom = async () => {
      try {
        //   6 ( )
        const response = await axios.get(`${process.env.REACT_APP_API_URL}/rooms`, {
          headers: {
            'Cache-Control': 'no-cache',
            'Pragma': 'no-cache',
            'Expires': '0'
          }
        });
        const rooms = response.data.data;
        
        //   6
        const standardRoom = rooms.find(room => room.roomNumber === 6);
        
        if (standardRoom) {
          setRoom(standardRoom);
        }
      } catch (error) {
        console.error('  :', error);
      } finally {
        setLoading(false);
      }
    };

    fetchRoom();
  }, []);

  return (
    <Box>
      {/*   */}
      <Box 
        sx={{ 
          textAlign: 'center', 
          mb: 6,
          mt: 2
        }}
      >
        <Typography variant="h3" component="h1" gutterBottom>
              79
        </Typography>
        <Typography variant="h5" color="text.secondary" paragraph>
               
        </Typography>
        <Button 
          variant="contained" 
          size="large" 
          component={Link} 
          to="/booking"
          sx={{ mt: 2 }}
        >
           
        </Button>
      </Box>

      {/*    */}
      <Paper elevation={3} sx={{ p: 3, mb: 6 }}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={6}>
            <Typography variant="h5" gutterBottom>
              <HotelIcon sx={{ mr: 1, verticalAlign: 'middle' }} />
               
            </Typography>
            <Typography paragraph>
                79    ,     , ,   .
            </Typography>
            <Typography paragraph>
                    ,   ,  .
            </Typography>
            <Typography paragraph>
                  , , ,    Wi-Fi .
            </Typography>
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography variant="h5" gutterBottom>
              <LocationIcon sx={{ mr: 1, verticalAlign: 'middle' }} />
                
            </Typography>
            <Typography paragraph>
              <strong>:</strong>   79,  
            </Typography>
            <Typography paragraph>
              <strong>:</strong> <PhoneIcon sx={{ fontSize: 'small', verticalAlign: 'middle' }} /> 03-1234567
            </Typography>
            <Typography paragraph>
              <strong>:</strong> diamshotels@gmail.com
            </Typography>
            <Typography paragraph>
              <strong> :</strong> 24/7
            </Typography>
          </Grid>
        </Grid>
      </Paper>

      {/*   */}
      <Typography variant="h4" component="h2" gutterBottom textAlign="center">
         
      </Typography>
      <Divider sx={{ mb: 4 }} />

      {loading ? (
        <Typography>...</Typography>
      ) : room ? (
        <Card sx={{ maxWidth: 800, mx: 'auto' }}>
          <CardMedia
            component="img"
            height="300"
            image={room.images.find(img => img.isPrimary)?.url || room.images[0]?.url || 'https://via.placeholder.com/800x400?text=+'}
            alt={` ${room.roomNumber}`}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
               {room.roomNumber} - {room.type === 'standard' ? '' : room.type}
            </Typography>
            <Typography variant="body2" color="text.secondary" paragraph>
              {room.description}
            </Typography>
            <Typography variant="body1">
              <strong>:</strong> {room.basePrice}  
            </Typography>
            <Typography variant="body2">
              <strong> :</strong> {room.maxOccupancy} 
            </Typography>
            <Box sx={{ mt: 2 }}>
              <Typography variant="subtitle1">
                <strong> :</strong>
              </Typography>
              <Grid container spacing={1}>
                {room.amenities.map((amenity, index) => (
                  <Grid item key={index}>
                    <Paper sx={{ px: 1, py: 0.5 }}>
                      {amenity}
                    </Paper>
                  </Grid>
                ))}
              </Grid>
            </Box>
          </CardContent>
          <CardActions>
            <Button 
              size="large" 
              variant="contained" 
              fullWidth
              component={Link}
              to={`/room/${room._id}`}
            >
               
            </Button>
          </CardActions>
        </Card>
      ) : (
        <Typography textAlign="center">    .</Typography>
      )}
    </Box>
  );
};

export default HomePage; 