import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { toast } from 'react-toastify';
import { 
  Box, 
  Typography, 
  Paper, 
  Table, 
  TableBody, 
  TableCell, 
  TableContainer, 
  TableHead, 
  TableRow,
  TablePagination,
  Button,
  IconButton,
  Chip,
  TextField,
  Grid,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  CircularProgress,
  Tooltip
} from '@mui/material';
import { 
  Edit as EditIcon,
  Delete as DeleteIcon,
  Visibility as ViewIcon,
  Print as PrintIcon,
  FilterList as FilterIcon,
  Search as SearchIcon
} from '@mui/icons-material';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { he } from 'date-fns/locale';

const BookingsListPage = () => {
  const [bookings, setBookings] = useState([]);
  const [loading, setLoading] = useState(true);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [bookingToDelete, setBookingToDelete] = useState(null);
  const [viewDialogOpen, setViewDialogOpen] = useState(false);
  const [editDialogOpen, setEditDialogOpen] = useState(false);
  const [selectedBooking, setSelectedBooking] = useState(null);
  const [filters, setFilters] = useState({
    startDate: null,
    endDate: null,
    status: ''
  });
  const [showFilters, setShowFilters] = useState(false);

  //  
  useEffect(() => {
    fetchBookings();
  }, []);

  const fetchBookings = async () => {
    try {
      setLoading(true);
      
      //     
      let queryParams = '';
      
      if (filters.startDate && filters.endDate) {
        queryParams += `startDate=${filters.startDate.toISOString()}&endDate=${filters.endDate.toISOString()}`;
      }
      
      if (filters.status) {
        queryParams += `${queryParams ? '&' : ''}status=${filters.status}`;
      }
      
      const url = `${process.env.REACT_APP_API_URL}/bookings${queryParams ? `?${queryParams}` : ''}`;
      const response = await axios.get(url);
      
      setBookings(response.data.data);
    } catch (error) {
      console.error('  :', error);
      toast.error('  .     .');
    } finally {
      setLoading(false);
    }
  };

  //   
  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  //     
  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  //   
  const handleOpenDeleteDialog = (booking) => {
    setBookingToDelete(booking);
    setDeleteDialogOpen(true);
  };

  //   
  const handleCloseDeleteDialog = () => {
    setDeleteDialogOpen(false);
    setBookingToDelete(null);
  };

  const handleViewBooking = (booking) => {
    setSelectedBooking(booking);
    setViewDialogOpen(true);
  };
  
  const handleCloseViewDialog = () => {
    setViewDialogOpen(false);
    setSelectedBooking(null);
  };
  
  const handleEditBooking = (booking) => {
    setSelectedBooking(booking);
    setEditDialogOpen(true);
  };
  
  const handleCloseEditDialog = () => {
    setEditDialogOpen(false);
    setSelectedBooking(null);
  };
  
  const handleSaveBooking = async () => {
    try {
      //      
      toast.success('  ');
      setEditDialogOpen(false);
      fetchBookings(); //  
    } catch (error) {
      console.error('  :', error);
      toast.error('  ');
    }
  };

  //  
  const handleDeleteBooking = async () => {
    if (!bookingToDelete) return;
    
    try {
      await axios.delete(`${process.env.REACT_APP_API_URL}/bookings/${bookingToDelete._id}`);
      
      //  
      setBookings(bookings.filter(booking => booking._id !== bookingToDelete._id));
      
      toast.success('  ');
    } catch (error) {
      console.error('  :', error);
      toast.error('  .     .');
    } finally {
      handleCloseDeleteDialog();
    }
  };

  //   
  const handleFilterChange = (name, value) => {
    setFilters(prev => ({
      ...prev,
      [name]: value
    }));
  };

  //  
  const applyFilters = () => {
    fetchBookings();
  };

  //  
  const resetFilters = () => {
    setFilters({
      startDate: null,
      endDate: null,
      status: ''
    });
    
    //    
    fetchBookings();
  };

  //  
  const handlePrintInvoice = async (bookingId) => {
    try {
      //      
      const token = localStorage.getItem('token');

      if (!token) {
        toast.error('  ');
        return;
      }

      //  URL    
      const invoiceUrl = `${process.env.REACT_APP_API_URL}/invoices/${bookingId}?token=${token}`;
      
      //    
      window.open(invoiceUrl, '_blank');
    } catch (error) {
      console.error('  :', error);
      toast.error('  .     .');
    }
  };

  //  
  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('he-IL');
  };

  //   
  const getPaymentStatusChip = (status) => {
    return (
      <Chip 
        label={status === 'paid' ? '' : ''} 
        color={status === 'paid' ? 'success' : 'warning'}
        size="small"
      />
    );
  };

  //   
  const getPaymentMethodText = (method) => {
    switch (method) {
      case 'credit':
        return ' ';
      case 'cash':
        return '';
      case 'bank_transfer':
        return ' ';
      default:
        return method;
    }
  };

  return (
    <Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4" component="h1">
           
        </Typography>
        
        <Button
          variant="outlined"
          startIcon={<FilterIcon />}
          onClick={() => setShowFilters(!showFilters)}
        >
          {showFilters ? ' ' : ' '}
        </Button>
      </Box>
      
      {/*  */}
      {showFilters && (
        <Paper sx={{ p: 2, mb: 3 }}>
          <Grid container spacing={2} alignItems="center">
            <Grid item xs={12} sm={3}>
              <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={he}>
                <DatePicker
                  label=""
                  value={filters.startDate}
                  onChange={(date) => handleFilterChange('startDate', date)}
                  renderInput={(params) => <TextField {...params} fullWidth size="small" />}
                />
              </LocalizationProvider>
            </Grid>
            <Grid item xs={12} sm={3}>
              <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={he}>
                <DatePicker
                  label=" "
                  value={filters.endDate}
                  onChange={(date) => handleFilterChange('endDate', date)}
                  renderInput={(params) => <TextField {...params} fullWidth size="small" />}
                />
              </LocalizationProvider>
            </Grid>
            <Grid item xs={12} sm={3}>
              <TextField
                select
                fullWidth
                size="small"
                label=" "
                value={filters.status}
                onChange={(e) => handleFilterChange('status', e.target.value)}
                SelectProps={{
                  native: true
                }}
              >
                <option value=""></option>
                <option value="paid"></option>
                <option value="pending"></option>
              </TextField>
            </Grid>
            <Grid item xs={12} sm={3}>
              <Box sx={{ display: 'flex', gap: 1 }}>
                <Button
                  variant="contained"
                  startIcon={<SearchIcon />}
                  onClick={applyFilters}
                >
                  
                </Button>
                <Button
                  variant="outlined"
                  onClick={resetFilters}
                >
                  
                </Button>
              </Box>
            </Grid>
          </Grid>
        </Paper>
      )}
      
      {/*   */}
      <Paper>
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>' </TableCell>
                <TableCell> </TableCell>
                <TableCell></TableCell>
                <TableCell> '-</TableCell>
                <TableCell> '-</TableCell>
                <TableCell> </TableCell>
                <TableCell> </TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {loading ? (
                <TableRow>
                  <TableCell colSpan={9} align="center">
                    <CircularProgress size={40} sx={{ my: 2 }} />
                  </TableCell>
                </TableRow>
              ) : bookings.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={9} align="center">
                      
                  </TableCell>
                </TableRow>
              ) : (
                bookings
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map((booking) => (
                    <TableRow key={booking._id}>
                      <TableCell>{booking._id.substring(0, 8)}...</TableCell>
                      <TableCell>{booking.guest.name}</TableCell>
                      <TableCell>{booking.room.roomNumber}</TableCell>
                      <TableCell>{formatDate(booking.checkIn)}</TableCell>
                      <TableCell>{formatDate(booking.checkOut)}</TableCell>
                      <TableCell>{getPaymentStatusChip(booking.paymentStatus)}</TableCell>
                      <TableCell>{getPaymentMethodText(booking.paymentMethod)}</TableCell>
                      <TableCell>{booking.totalPrice.toFixed(2)} </TableCell>
                      <TableCell>
                        <Box sx={{ display: 'flex' }}>
                          <Tooltip title=" ">
                            <IconButton 
                              size="small" 
                              color="primary"
                              onClick={() => handleViewBooking(booking)}
                            >
                              <ViewIcon fontSize="small" />
                            </IconButton>
                          </Tooltip>
                          <Tooltip title="">
                            <IconButton 
                              size="small" 
                              color="secondary"
                              onClick={() => handleEditBooking(booking)}
                            >
                              <EditIcon fontSize="small" />
                            </IconButton>
                          </Tooltip>
                          <Tooltip title="">
                            <IconButton 
                              size="small" 
                              color="error"
                              onClick={() => handleOpenDeleteDialog(booking)}
                            >
                              <DeleteIcon fontSize="small" />
                            </IconButton>
                          </Tooltip>
                          <Tooltip title=" ">
                            <IconButton 
                              size="small" 
                              color="info"
                              onClick={() => handlePrintInvoice(booking._id)}
                            >
                              <PrintIcon fontSize="small" />
                            </IconButton>
                          </Tooltip>
                        </Box>
                      </TableCell>
                    </TableRow>
                  ))
              )}
            </TableBody>
          </Table>
        </TableContainer>
        
        <TablePagination
          rowsPerPageOptions={[5, 10, 25]}
          component="div"
          count={bookings.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
          labelRowsPerPage=" :"
          labelDisplayedRows={({ from, to, count }) => `${from}-${to}  ${count}`}
        />
      </Paper>
      
      {/*   */}
      <Dialog
        open={deleteDialogOpen}
        onClose={handleCloseDeleteDialog}
      >
        <DialogTitle> </DialogTitle>
        <DialogContent>
          <DialogContentText>
                    {bookingToDelete?.guest.name}?
                .
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDeleteDialog}></Button>
          <Button onClick={handleDeleteBooking} color="error" autoFocus>
            
          </Button>
        </DialogActions>
      </Dialog>
      
      {/*    */}
      <Dialog
        open={viewDialogOpen}
        onClose={handleCloseViewDialog}
        maxWidth="md"
        fullWidth
      >
        <DialogTitle> </DialogTitle>
        <DialogContent>
          {selectedBooking && (
            <Box sx={{ pt: 2 }}>
              <Grid container spacing={2}>
                <Grid item xs={12} sm={6}>
                  <Typography variant="subtitle2"> :</Typography>
                  <Typography>: {selectedBooking.guest.name}</Typography>
                  <Typography>: {selectedBooking.guest.phone}</Typography>
                  <Typography>: {selectedBooking.guest.email}</Typography>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <Typography variant="subtitle2"> :</Typography>
                  <Typography> : {selectedBooking.room.roomNumber}</Typography>
                  <Typography>: {selectedBooking.room.type === 'standard' ? '' : selectedBooking.room.type}</Typography>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <Typography variant="subtitle2"> :</Typography>
                  <Typography> '-: {formatDate(selectedBooking.checkIn)}</Typography>
                  <Typography> '-: {formatDate(selectedBooking.checkOut)}</Typography>
                  <Typography> : {selectedBooking.nights}</Typography>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <Typography variant="subtitle2"> :</Typography>
                  <Typography> : {selectedBooking.totalPrice.toFixed(2)} </Typography>
                  <Typography> : {selectedBooking.paymentStatus === 'paid' ? '' : ''}</Typography>
                  <Typography> : {getPaymentMethodText(selectedBooking.paymentMethod)}</Typography>
                </Grid>
                {selectedBooking.notes && (
                  <Grid item xs={12}>
                    <Typography variant="subtitle2">:</Typography>
                    <Typography>{selectedBooking.notes}</Typography>
                  </Grid>
                )}
              </Grid>
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseViewDialog}></Button>
          <Button 
            color="primary" 
            onClick={() => {
              handleCloseViewDialog();
              handleEditBooking(selectedBooking);
            }}
          >
            
          </Button>
          <Button 
            color="info"
            onClick={() => handlePrintInvoice(selectedBooking._id)}
          >
             
          </Button>
        </DialogActions>
      </Dialog>
      
      {/*   */}
      <Dialog
        open={editDialogOpen}
        onClose={handleCloseEditDialog}
        maxWidth="md"
        fullWidth
      >
        <DialogTitle> </DialogTitle>
        <DialogContent>
          {selectedBooking && (
            <Box sx={{ pt: 2 }}>
              <Typography variant="body2" color="text.secondary" paragraph>
                    .       .
              </Typography>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <TextField
                    label=""
                    multiline
                    rows={4}
                    fullWidth
                    defaultValue={selectedBooking.notes || ''}
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    select
                    fullWidth
                    label=" "
                    value={selectedBooking.paymentStatus}
                    SelectProps={{
                      native: true
                    }}
                  >
                    <option value="paid"></option>
                    <option value="pending"></option>
                  </TextField>
                </Grid>
              </Grid>
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseEditDialog}></Button>
          <Button color="primary" onClick={handleSaveBooking}>
            
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default BookingsListPage; 